import { useEffect, useRef, useState } from 'react';
import YouTube from 'react-youtube';

const UURL = [
    { id: 1, url: "_0CXEqkwKG8", title: "# # ", des: "2023   ? l   !" },
]

const Promotion = () => {
    const MOVIE = useRef();

    const [url, setUrl] = useState('_0CXEqkwKG8');
    const [tit, setTit] = useState(0);
    const [auto, setAuto] = useState(0);

    const opts = {
        height: '100%',
        width: '100%',
        playerVars: {
            autoplay: auto,
            loop: 1,
            controls: 0,
            showinfo: 0,
            rel: 0,
            playsinline: 1,
            mute: 1,
        },

    };



    return (
        <section className="Promotion csc" ref={MOVIE}>
            <h2> </h2>
            <p>       .</p>
            <div className="container">
                <div className='movie' id='player' >
                    <YouTube videoId={url} opts={opts} className='u' />
                    <div className="movie_title">
                        {
                            UURL[tit].title
                        }
                    </div>
                </div>
                <ul className="des">
                    {
                        UURL.map((you, idx) => {
                            return (
                                <li key={you.id} onClick={() => {
                                    setUrl(you.url);
                                    setTit(idx);
                                    setAuto(1);
                                }} className={tit === idx ? "on" : ""}
                                >

                                    <div className='tit'>{you.title}</div>
                                    <p>{you.des}</p>
                                </li>
                            )
                        })
                    }
                </ul>
            </div>
        </section >
    )
}
export default Promotion;