import React from 'react'
const NEWSTW = [
    { id: 1, category: "", title: "    ", mtitle: "   !", link: "#!", src: "/assets/images/main_newstw01.jpg" },
    { id: 2, category: "", title: "   ! ", mtitle: "   ", link: "#!", src: "/assets/images/main_newstw02.jpg" },
    { id: 3, category: "", title: "  20   ", mtitle: "  ", link: "#!", src: "/assets/images/main_newstw03.jpg" },
]

const Newstw = () => {
    return (
        <section className="Newstw">
            <div className="inner">
                {
                    NEWSTW.map(newstw => {
                        return (
                            <figure key={newstw.id}>
                                <a href={newstw.link}>
                                    <div className="box">
                                        <img src={process.env.PUBLIC_URL + newstw.src} alt={newstw.title} />
                                    </div>
                                    <strong>{newstw.category}</strong>
                                    <div className="title">{newstw.title}</div>
                                    <span>{newstw.mtitle}</span>
                                </a>
                            </figure>
                        )
                    })
                }
            </div>
        </section>
    )
}



export default Newstw