import React from 'react'
const Smenu02 = ({ content, num }) => {
    return (
        <smenu className='Smenu02'>

            <div className="tit">
                <strong>{content[num].tit}</strong>
            </div>
            <div className='bg'>
                <div className="case">
                    <h2>
                        {content[num].yb}<br /></h2>
                    <p>{content[num].ybs}</p>

                </div>
            </div>

            <div className="sub__dev">
                {content[num].dev}
            </div>
            <p className="sub__des">
                {content[num].des}
            </p>

            <section className="sub">
                <div className="sub__tit">
                    <h2></h2>
                    <p></p>
                    <strong> </strong>
                </div>
                <ul className="sub_ttt">
                    <li><a href=""></a></li>
                    <li><a href="">OT</a></li>
                </ul>
                <div className="check">
                    <input type='checkbox' name='check' value='(PC/)' />(PC/)<span>0</span><br />
                    <input type='checkbox' name='check' value='+' />+<span>0</span><br />
                    <input type='checkbox' name='check' value='(PC/)+' />(PC/)+<span>0</span>
                </div>


                <ul className="sub__service">
                    <li className='mtxt'>   </li>
                    <li className='mtit'>7( 20)    PC</li>
                    <li className='gy'><span></span>  []</li>
                </ul>
            </section>
            <div className="sub__dev_gibon">
                {content[num].gib}
            </div>

            <section className="sub">
                <div className="sub__tit">
                    <h2></h2>
                    <p></p>
                    <strong> </strong>
                </div>
                <ul className="sub_ttt_gibon">
                    <li><a href=""></a></li>
                    <li><a href="">OT</a></li>
                </ul>
                <div className="check_gibon">
                    <input type='checkbox' name='check' value='(PC/)' />(PC/)<span>0</span><br />
                    <input type='checkbox' name='check' value='+' />+<span>0</span><br />
                    <input type='checkbox' name='check' value='(PC/)+' />(PC/)+<span>0</span>
                </div>
                <ul className="sub__service">
                    <li className='mtxt'>[2023]  </li>
                    <li className='mtit'>60( 150)    PC</li>
                    <li className='gy'><span></span>2022  1-(2) [  ]</li>
                </ul>
            </section>
        </smenu >
    )
}

export default Smenu02;